body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: #f4f4f4;
  color: #333;
  line-height: 1.6;
}

.resume-container {
  max-width: 900px;
  margin: 20px auto;
  padding: 20px;
  background: white;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.resume-header {
  background: #2c3e50;
  color: white;
  padding: 20px;
  text-align: center;
  border-radius: 8px 8px 0 0;
}

.resume-header h1 {
  margin: 0;
  font-size: 2.5em;
  letter-spacing: 1px;
}

.resume-header p {
  margin: 5px 0;
  font-size: 1.2em;
}

.resume-section {
  padding: 20px;
  border-bottom: 1px solid #ddd;
  transition: background 0.3s ease;
}

.resume-section:hover {
  background: #f9f9f9;
}

.resume-section h2 {
  font-size: 1.8em;
  color: #2c3e50;
  margin-top: 0;
  display: flex;
  align-items: center;
}

.resume-section h2 svg {
  margin-right: 10px;
}

/* Card design for experience */
.card {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 20px;
}

.card:hover {
  transform: scale(1.03);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2);
}

/* Experience list without bullet points */
.experience-list {
  list-style-type: disc; /* Adds bullets */
  padding-left: 20px; /* Aligns bullets with a little indentation */
  margin: 0;
  text-align: left;
}

.experience-list li {
  padding-left: 5px; /* Ensures text is aligned with bullets */
  margin-bottom: 10px;
  line-height: 1.5;
}

/* Experience list without bullet points */
.experience-sub-list {
  list-style-type: square; /* Adds bullets */
  padding-left: 30px; /* Aligns bullets with a little indentation */
  margin: 0;
  text-align: left;
}

.experience-sub-list li {
  padding-left: 7px; /* Ensures text is aligned with bullets */
  margin-bottom: 5px;
  line-height: 1.5;
}


/* Remove any hover effects from list items */
.experience-list li:hover {
  transform: none;
  box-shadow: none;
}

/* Flexbox for skills */
.skills-flexbox {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  align-items: center;
}

.skill-item {
  background: #2c3e50;
  color: white;
  padding: 8px 12px;
  border-radius: 5px;
  text-align: center;
  font-size: 0.9em;
  transition: background 0.3s, transform 0.3s;
}

.skill-item:hover {
  background: #34495e;
  transform: scale(1.1);
}
