/* src/HomePage.css */
body, html {
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
    background: #f4f4f4;
    text-align: center;
    color: #333;
  }

.homepage {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
}

.header {
  background: #2c3e50;
  color: white;
  padding: 20px 20px;
  flex: 1;
}

.header h1 {
  margin: 0;
  font-size: 3em;
  letter-spacing: 2px;
}

.header p {
  font-size: 1.25em;
  margin-top: 10px;
}

.btn {
  display: inline-block;
  margin-top: 20px;
  padding: 10px 20px;
  background: #e74c3c;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: background 0.3s ease;
}

.btn:hover {
  background: #c0392b;
}

.content {
  padding: 40px 40px;
  flex: 1;
  background: white;
}

.content h2 {
  margin-top: 0;
}

.footer {
  padding: 20px;
  background: #2c3e50;
  color: white;
}

.footer a {
  margin: 0 10px;
  color: #ecf0f1;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer a:hover {
  color: #e74c3c;
}
  
.construction-img {
    width: 10%; 
    height: auto;
    margin: 10px 0; /* Ensure margins only apply equally above and below */
    border-radius: 25px;
}
  
.breaking {
  color: red;
  font-weight: bold;
}