/* src/DropdownMenu.css */
.dropdown-container {
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 1000;
  }
  
  .menu-button {
    background: #2c3e50;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background 0.3s ease;
  }
  
  .menu-button:hover {
    background: #34495e;
  }
  
  .dropdown-menu {
    background: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    padding: 0;
    list-style: none;
    margin: 10px 0 0;
    position: absolute;
    left: 0;
  }
  
  .dropdown-menu li {
    padding: 10px;
    text-align: left;
  }
  
  .dropdown-menu li a {
    text-decoration: none;
    color: #333;
    display: block;
  }
  
  .dropdown-menu li a:hover {
    background: #f4f4f4;
  }
  